@font-face {
    font-family: 'Sans_a4fran3';
    src: local('Sans_a4fran3'), url(../fonts/Sans_a4fran3.woff) format('woff');
}
.homePageStyle {

}
.onvan{
    text-align:right;
    line-height: 38;
    color:#FFFFFF;
    background-image:url('../../assets/images/slide1.jpg');
    background-repeat: no-repeat;
    background-size: 1897px 700px;
    padding-bottom: 30px;
    width:100%;
    height:100%;
}
.oPadding{
    padding-bottom:305px;
}
.ant-image{
    display:block;
}
.onServiceProcess{
    text-align:right;
    padding-top:12%;
    margin-right: 10px;
    line-height: 8;
    color:#FFFFFF;
    background-image:url('https://api.kiangroup.com/media/9e8a14e7-9afa-4f2f-8780-957c06ea89e6_MonMar062023.png');
    background-repeat:no-repeat;
    width:100%;
    height:100%;
}
.transbox {
    margin: 0px;
    background-color: #364d79;
    border: 1px solid black;
    border-radius: 2px;
    opacity: 0.8;
    width:100%;
    height:100%;
}
.onvan_2{
    font-family: 'Sans_a4fran3'!important;
    text-align:center;
    margin-right: 10px;
    line-height: 10;
    color:#FFFFFF;
    background-image:url('../../assets/images/IMG_8755222.jpg');
    width:100%;
    height:100%;
    background-attachment:fixed;
    background-position:top;
    background-repeat:no-repeat;
    background-size:cover;
}
.onvan_3{
    font-family: 'Sans_a4fran3'!important;
    text-align:right;
    padding-top:12%;
    margin-right: 10px;
    line-height: 0;
    color:#FFFFFF;
    background-image:url('https://api.kiangroup.com/media/838ffeaf-11a2-4c5d-9472-ee633422f368_WedMar012023.png');
    background-size: cover;
    width: 100%;
    height: 300px;
    object-fit: cover;
    max-width: 50%;
    max-height: 100%;

}

.onvan_4 {
    font-family: 'Sans_a4fran3'!important;
    width: 768px;
    height: 600px;
    background-image: url('../../assets/images/news1.png');
    background-size: 100% 100%;
    line-height: 30;
    border-radius: 5px;
    background-repeat:no-repeat;
}
.onvan_news2 {
    font-family: 'Sans_a4fran3'!important;
    width: 768px;
    height: 600px;
    background-image: url('../../assets/images/news2.jpg');
    background-size: 100% 100%;
    line-height: 30;
    border-radius: 5px;
    background-repeat:no-repeat;
}
.width-large {
    font-family: 'Sans_a4fran3'!important;
    padding-right:min(100px , 6%) !important;
    padding-left:min(100px , 6%) !important;
}

.height-large {
    font-family: 'Sans_a4fran3'!important;
    padding-top:60px !important;
    padding-bottom:100px !important;

}
.height-large2 {
    font-family: 'Sans_a4fran3'!important;
    padding-top:50px !important;
    padding-bottom:50px !important;

}
.homePageImage {
    width: 600px;
}
.cardAnt {
    font-family:Sans_a4fran3 !important;
    background-color:white;
    padding: 20px 50px;
}
.ant-card .ant-card-cover >*{
    margin: auto;
    width: 35%;
    padding: 10px;

}
.col-line{
    line-height: 1.6;
}

.aboutImage1{
    font-family: 'Sans_a4fran3'!important;
    width: 100%;
    height: 100%;
    background-image: url('https://api.kiangroup.com/media/c641e65c-4b35-415d-bcd1-5f82106ccfe3_TueFeb142023.png');
    background-size: 100% 100%;
    line-height: 40;
    background-repeat:no-repeat;
    transform: scale(1.1);
    padding: 50px 50px;

}
.ceoImageandMessage{
    font-family: 'Sans_a4fran3'!important;
    text-align:right;
    margin-right: 10px;
    line-height: 40;
    color:#FFFFFF;
    background-image:url('../../assets/images/IMG_8755222.jpg');
    width:100%;
    height:100%;
    background-attachment:fixed;
    background-position:top;
    background-repeat:no-repeat;
    background-size:cover;

}
.content-section{
    padding: 32px 16px;
    border: 3px solid #89B7EE;
    background: #fff;
    margin-bottom: 10px;
}
.displayMobile{
    display: none;
}
.text-center {
    justifyContent: center;
}
#table{
    width: 100%;
}

tr {
    display: inline;
}

td {
    margin: 0;
    padding: 0;
    width: 50%;
    float: left;
    background: #d8d8d8;
}
.menubutton{
    border: 0px solid #004B99;
    color: #fff!important;
    background-color: #004B99;
    border-radius: 5px;
    padding: 8px 16px;
    height: 47px;
    margin-left:10px;
    display: block;
    font-weight: 500;
    line-height: 30px;
    text-align: center;
}

/* revert/restore properties on larger screen */
@media all and (min-width: 900px) {
    tr {
        display: table-row;
    }
    td {
        float: none;
        min-width: 300px;
    }
}

