/**************************For Menu*****************************/
* {
    padding: 0;
    box-sizing: border-box;
    font-family:Sans_a4fran3 !important;
}

body {
    background-color: #f3f3f7;
}

header {
    background-color: #f3f3f7;
    padding: 0 5%;
}

header nav {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
}

.logo {
    flex: 2;
    display: flex;
    align-items: center;
}

.logo a {
    text-decoration: none;
    font-size: 26px;
    color: #fff;
    text-transform: uppercase;
    font-weight: 800;
}

.bartoggle,
#menubrop {
    display: none;
}

.NavMenu {
    direction: rtl;
    flex: 10;
    list-style: none;
    position: relative;
    justify-content: end;
}

.NavMenu ul {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -270px !important;
    width: 1900px;
}
.NavMenu li {
    display: inline-block;

}

.NavMenu li input {
    display: none;
}

.NavMenu li a {
    display: block;
    padding: 20px 14px;
    font-size: 17px;
    text-decoration: none;
    text-transform: uppercase;
    color: #000;
    position: relative;
    font-family: 'Sans_a4fran3';
}

.NavMenu li a label {
    cursor: pointer;
    appearance: none;
    display: block;
    position: relative;
}

.NavMenu li a label::after {
    content: "+";
    position: absolute;
    right: 0;
    top: 0;
    font-size: 20px;
}

.NavMenu>li>a label::after {
    right: -15px;
    top: -3px;
}

.NavMenu li ul {
    position: absolute;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    min-width: 200px;
    border-bottom: 2px solid #2874f0;
    top: 100%;
    box-shadow: 0 3px 5px rgb(0 0 0/20%);
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -ms-transition: all 0.5s;
    -o-transition: all 0.5s;
    transform: translateY(10px);
    -webkit-transform: translateY(10px);
    -moz-transform: translateY(10px);
    -ms-transform: translateY(10px);
    -o-transform: translateY(10px);
    visibility: hidden;
    opacity: 0;
}

.NavMenu li ul li {
    position: relative;
}

.NavMenu li ul li a {
    color: #000000;
    padding: 8px 10px;
    display: block;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -ms-transition: all 0.5s;
    -o-transition: all 0.5s;
}

.NavMenu li ul li ul {
    position: absolute;
    left: 100%;
    top: 0;
}

@media(min-width:992px) {
    .NavMenu li ul li a:hover {
        border-right: 2px solid #89B7EE;
        color: #89B7EE;
        transition: all 0.5s;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -ms-transition: all 0.5s;
        -o-transition: all 0.5s;

    }

    .NavMenu li:hover>ul,
    .NavMenu li ul li:hover>ul {
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
        -webkit-transform: translateY(0);
        -moz-transform: translateY(0);
        -ms-transform: translateY(0);
        -o-transform: translateY(0);
    }
}

@media(max-width:991.98px) {
    header {
        padding: 0px 0px;
    }

    .logo {
        flex: 6;
    }

    .bartoggle {
        display: flex;
        justify-content: center;
        font-size: 30px;
        align-items: center;
        background-color: #fff;
        padding: 0 10px;
        cursor: pointer;
    }

    .NavMenu {
        flex: 12;
        position: fixed;
        flex-direction: column;
        background-color: #2874f0;
        left: 0;
        top: 40px;
        height: 100vh;
        z-index: -1;
        padding: 15px 0 50px 0;
        justify-content: start;
        overflow-y: scroll;
        transition: all 0.5s;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -ms-transition: all 0.5s;
        -o-transition: all 0.5s;
        transform: translateX(-100%);
        -webkit-transform: translateX(-100%);
        -moz-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
        -o-transform: translateX(-100%);
    }

    .NavMenu li ul,
    .NavMenu li ul li ul {
        position: initial;
        left: 0;
        visibility: visible;
        opacity: 1;
        top: 0;
        display: none;
    }

    .NavMenu li a {
        padding: 8px 15px;
        border-bottom: 1px solid #fff;
    }

    .NavMenu li ul li ul {
        background: #2874f0;
        position: inherit;
        margin-top: -10px;
    }

    .NavMenu li ul li ul li a {
        font-size: 14px;
        color: #fff;
        font-weight: 400;
        text-transform: initial;
        padding: 7px 15px 7px 30px;
    }

    .NavMenu li a label::after {
        right: 10px;
    }

    .NavMenu li input:checked+ul,
    .NavMenu li ul li input:checked+ul {
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;
    }

    input:checked+.NavMenu {
        transform: translateX(0);
        -webkit-transform: translateX(0);
        -moz-transform: translateX(0);
        -ms-transform: translateX(0);
        -o-transform: translateX(0);
    }

}

@media(max-width:768px) {
    .NavMenu {
        width: 100%;
    }
}

