@media screen and (max-width: 420px) {
  .App{
    /*position: absolute;*/
    left: 0;
    background-color: #f3f3f7;
  }
  .class-page {
    top: 50%;
  }
  .topnav.responsive {position: relative;}
  .topnav.responsive a.icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
  .displayNone{
