@charset "UTF-8";

@font-face {
  font-family: 'IranSans';
  src: url('./assets/fonts/iransansfanum/eot/IRANSansWeb(FaNum).eot');
  src: local('./assets/fonts/iransansfanum'), url('./assets/fonts/iransansfanum/woff/IRANSansWeb(FaNum).woff') format('woff'), url('./assets/fonts/iransansfanum/ttf/IRANSansWeb(FaNum).ttf') format('truetype');
}
body, body * {
  font-family: 'IranSans' !important;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;


}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
